/* eslint-disable */
import React from 'react';

import IconWaterDrop from "../assets/water-drop.svg";
import IconGears from "../assets/gears.svg";
import IconRecycle from "../assets/recycle.svg";
import IconFlask from "../assets/flask.svg";

const WorkDirectionsSection = (props) => (
	<section id={'word-directions-section'} className="flex-column">
		<div className="part-1 centered py-100 px-15">
			<div className="content flex-column flex-align-center">
				<span className="title text-center">НАПРАВЛЕНИЯ РАБОТ</span>
				<div className="hr-line mt-20 w-50" />
				<ul className="card-list flex-space-between mt-60">
					<li className="card card-1 flex-column flex-align-center">
						<img src={IconWaterDrop} className="icon w-66 h-66"/>
						<span className="title mt-40">ПТП</span>
						<span className="caption mt-10 text-center mb-20">
						Производство и применение противотурбулентной присадки, торговая марка «Нара»
					</span>
						<span className="link centered pointer mt-auto px-12 py-6">ПОДРОБНЕЕ</span>
					</li>
					<li className="card card-2 flex-column flex-align-center">
						<img src={IconGears} className="icon w-66 h-66"/>
						<span className="title mt-40">Оборудование</span>
						<span className="caption mt-10 text-center mb-20">
						Производство инжекционного дозирующего оборудования для закачки присадок
					</span>
						<span className="link centered pointer mt-auto px-12 py-6">ПОДРОБНЕЕ</span>
					</li>
					<li className="card card-3 flex-column flex-align-center">
						<img src={IconRecycle} className="icon w-66 h-66"/>
						<span className="title mt-40">Переработка</span>
						<span className="caption mt-10 text-center mb-20">
						Измельчение полимерных материалов, включая тонкое измельчение, для нового применения или утилизации
					</span>
						<span className="link centered pointer mt-auto px-12 py-6">ПОДРОБНЕЕ</span>
					</li>
					<li className="card card-4 flex-column flex-align-center">
						<img src={IconFlask} className="icon w-66 h-66"/>
						<span className="title mt-40">Криооборудование</span>
						<span className="caption mt-10 text-center mb-20">
						Реализация не стандартных решений в криотехнике и криотехнологиях
					</span>
						<span className="link centered pointer mt-auto px-12 py-6">ПОДРОБНЕЕ</span>
					</li>
				</ul>
			</div>
		</div>

		<div className="part-2 centered px-15">
			<div className="content flex-column flex-align-center">
				<span className="title text-center">ПТП «Нара» соответствует всем нормативным требованиям, применяемым к ПТП, что подтверждено:</span>
				<ul className="list flex-column flex-align-center mt-10">
					<li className="item item-1 text-center mt-20">Техническими условиями</li>
					<li className="item item-2 text-center mt-20">Паспортом безопасности</li>
					<li className="item item-3 text-center mt-20">Сертификатом соответствия ТЭКСерт</li>
					<li className="item item-4 text-center mt-20">Заключением и Отчетом АО «ВНИИНП»</li>
				</ul>
			</div>
		</div>
	</section>
);

export default WorkDirectionsSection;