/* eslint-disable */
import React from 'react';
import BgImage from "../assets/background.jpg";

class Homepage extends React.Component {
	constructor(props) {
		super(props);

		this.state = {};

	}

	render() {
		return (
			<section id={'homepage-section'} className="centered px-15">

				<div className="background absolute" style={{backgroundImage: `url('${BgImage}')`}}>
					<div className="inner-background" />
				</div>

				<header className="relative flex-column flex-align-center">

					<span className="title text-center mx-25 py-3">Delta P</span>
					<span className="caption text-center mt-10 pt-2 pb-5 mx-25">Производство противотурбулентных присадок.</span>
					<span className="description text-center mt-10">Все процессы, от синтеза катализатора до изготовления готовой продукции на территории РФ.</span>

				</header>

			</section>
		);
	}
}

Homepage.propTypes = {};

export default Homepage;