import React from 'react';

const AboutSection = (props) => (
	<section id={'about-section'} className="centered py-100 px-15">
		<div className="text flex-column flex-align-center">
			<span className="title">О НАС</span>
			<div className="hr-line mt-20 w-50" />
			<span className="description text-center mt-20">
				ООО «Дельта Р» - научно–производственная компания. Образована в 2008 г. Основное направление деятельности: производство и применение противотурбулентных присадок для нефте и газо продуктов. Изначально упор был сделан на разработку такой технологии и продукта, которые построены на использовании преимущественно отечественного сырья и оборудования. Это обеспечивает независимость производства от западных поставщиков сырья и технологий. Компания работает в сотрудничестве с Российской Академией Наук. В настоящее время завершена разработка и запущен в производство первый в линейке продуктов – противотурбулентная присадка, торговая марка «Нара». Пройденные опытно-промысловые испытания показывают высокую эффективность данного продукта. Головной офис и лабораторный центр ООО «Дельта – Р» расположены в г. Москве. В 2018 г. запущено производство в г. Тольятти.
			</span>
		</div>
	</section>
);

export default AboutSection;